function parse(fileTxt) {
  console.log('原始数据');
  console.log(fileTxt);
  src_store.dispatch('startParse');
  if (!fileTxt) {
    return false;
  }

  var atomsArr = fileTxt.split('\n'); // windows 平台换行符匹配有没问题

  var i = 0;
  var len = atomsArr.length;
  // 第一位原子数目、第二位分子名称需要参数校验
  var elesArr = [];

  for (i = 2; i < len; i++) {
    atomsArr[i] = atomsArr[i].replace(/\s+/g, ' ');
    var tmpArr = atomsArr[i].split(' ');
    // console.log(tmpArr)
    var tmpPos = {
      'x': parseFloat(tmpArr[1]),
      'y': parseFloat(tmpArr[3]),
      'z': parseFloat(tmpArr[2])
      // console.log(tmpPos)
    };atomsArr[i] = new Atom_Atom(tmpArr[0], tmpPos);
    if (elesArr.indexOf(atomsArr[i].atom) === -1) {
      elesArr.push(atomsArr[i].atom);
    }
    // console.log(atomsArr[i])
  }

  var eleKindsLen = elesArr.length;
  for (i = 0; i < eleKindsLen; i++) {
    var name = elesArr[i];
    elesArr[i] = {
      atom: name,
      radius: Atoms[name].radius,
      color: Atoms[name].color
    };
  }
  console.log(elesArr);
  var atomsNum = atomsArr.length;
  for (i = 2; i < atomsNum; i++) {
    for (var j = 2; j < atomsNum; j++) {
      if (j === i) {
        continue;
      }

      if (isBond(atomsArr[i], atomsArr[j])) {
        atomsArr[i].neighbors.push(atomsArr[j]);
        var bond = new Bond_Bond(atomsArr[i], atomsArr[j]);
        atomsArr[i].bonds.push(bond);
      }
    }
  }

  console.log('文件数据解析成功', atomsArr);
  //   console.log(store)
  src_store.dispatch('endParse');
  src_store.dispatch('updateMoleculeData', atomsArr);
  src_store.dispatch('updateElementsData', elesArr);
}

function getAtomDistance(pos1, pos2) {
  var distanceX2 = (pos1.x - pos2.x) * (pos1.x - pos2.x);
  var distanceY2 = (pos1.y - pos2.y) * (pos1.y - pos2.y);
  var distanceZ2 = (pos1.z - pos2.z) * (pos1.z - pos2.z);

  return Math.sqrt(distanceX2 + distanceY2 + distanceZ2);
}

function getBondMaxDistance(atom1, atom2) {
  return (atom1.radius / 100 + atom2.radius / 100) * 1.2;
}

function isBond(atom1, atom2) {
  var atomDistance = getAtomDistance(atom1.position, atom2.position);
  var maxBondDistance = getBondMaxDistance(atom1, atom2);
  return atomDistance < maxBondDistance;
}
